// src/components/RetailerLayout.jsx
import React from 'react';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  LayoutDashboard,
  Package,
  ShoppingCart,
  Bell,
  Search,
  Store, // Using this for the logo
} from "lucide-react";

// Helper NavLink component
function NavLink({ href, icon: Icon, children }) {
  const router = useRouter();
  const isActive = router.pathname.startsWith(href);

  return (
    <Link href={href} legacyBehavior>
      <a className="w-full">
        <Button
          variant={isActive ? "secondary" : "ghost"}
          className="w-full justify-start text-left"
        >
          <Icon className="mr-2 h-4 w-4" />
          {children}
        </Button>
      </a>
    </Link>
  );
}

export default function RetailerLayout({ children }) {
  return (
    <div className="grid min-h-screen w-full grid-cols-[250px_1fr]">
      {/* --- Sidebar --- */}
      <aside className="flex h-full max-h-screen flex-col gap-2 border-r bg-gray-900 text-white">
        <div className="flex h-[60px] items-center border-b px-6">
          <Link href="/retailer/dashboard" className="flex items-center gap-2 font-semibold">
            <Store className="h-6 w-6" />
            <span className="">Arik Dashboard</span>
          </Link>
        </div>
        <nav className="flex-1 overflow-auto px-4 py-2">
          <NavLink href="/retailer/dashboard" icon={LayoutDashboard}>
            DASHBOARD
          </NavLink>
          <NavLink href="/retailer/products" icon={Package}>
            ALL PRODUCTS
          </NavLink>
          <NavLink href="/retailer/orders" icon={ShoppingCart}>
            ORDER LIST
          </NavLink>
        </nav>
      </aside>

      {/* --- Main Content Area --- */}
      <div className="flex flex-col">
        {/* --- Top Header Bar --- */}
        <header className="flex h-[60px] items-center gap-4 border-b bg-white px-6">
          <div className="flex-1">
            <Search className="h-4 w-4 text-gray-500" />
          </div>
          <Button variant="ghost" size="icon" className="rounded-full">
            <Bell className="h-4 w-4" />
          </Button>
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon" className="rounded-full">
                <Avatar className="h-8 w-8">
                  <AvatarImage src="/path-to-avatar.png" alt="Admin" />
                  <AvatarFallback>A</AvatarFallback>
                </Avatar>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>My Account</DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem>Settings</DropdownMenuItem>
              <DropdownMenuItem>Logout</DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </header>

        {/* --- Page Content --- */}
        <main className="flex-1 overflow-auto bg-gray-100/50 p-6">
          {children}
        </main>
      </div>
    </div>
  );
}