// src/pages/retailer/products/new.js
import React, { useState } from 'react';
import { useRouter } from 'next/router';
import RetailerLayout from '../../../components/RetailerLayout';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertTriangle, Upload, Loader2 } from "lucide-react";

export default function NewProductPage() {
  const router = useRouter();
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    category: '',
    brand: '',
    sku: '',
    stock: '',
    price: '',
    salePrice: '', // We'll map this to 'price' for now
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData(prev => ({ ...prev, [id]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    // Prepare data for the API to match your Product schema
    // The form is simple, so we'll create one 'default' size entry.
    const productData = {
      name: formData.name,
      description: formData.description,
      category: formData.category,
      brand: formData.brand,
      price: Number(formData.salePrice || formData.price), // Use sale price if available
      // Your model expects 'sizes' to be an array
      sizes: [
        {
          size: "default", // Using a default size for this simple form
          sku: formData.sku,
          stock: Number(formData.stock)
        }
      ],
      // We can add more fields like warehouses here later
    };

    try {
      // --- THIS IS THE FIX ---
      const token = localStorage.getItem("token"); //
      if (!token) {
        throw new Error("No authorization token found. Please log in again.");
      }
      const authHeaders = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}` //
      };
      // --- END FIX ---

      const res = await fetch('/api/retailer/products', {
        method: 'POST',
        headers: authHeaders,
        body: JSON.stringify(productData),
      });

      if (!res.ok) {
        const errData = await res.json();
        throw new Error(errData.error || 'Failed to create product');
      }

      // Success! Redirect back to the products list
      router.push('/retailer/products');

    } catch (err) {
      console.error(err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <RetailerLayout>
      <form onSubmit={handleSubmit}>
        <div className="flex items-center justify-between mb-6">
          <h1 className="text-3xl font-bold">Add New Product</h1>
          <div className="flex gap-2">
            <Button variant="outline" type="button" onClick={() => router.push('/retailer/products')}>
              Cancel
            </Button>
            <Button type="submit" disabled={loading}>
              {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              {loading ? "Saving..." : "Save Product"}
            </Button>
          </div>
        </div>

        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertTriangle className="h-4 w-4" />
            <AlertTitle>Error</AlertTitle>
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Column: Product Details */}
          <Card className="lg:col-span-2">
            <CardHeader>
              <CardTitle>Product Details</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="name">Product Name</Label>
                <Input id="name" value={formData.name} onChange={handleChange} placeholder="Lorem Ipsum" required />
              </div>
              <div className="space-y-2">
                <Label htmlFor="description">Description</Label>
                <Textarea id="description" value={formData.description} onChange={handleChange} placeholder="Lorem Ipsum Is A Dummy Text" />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="category">Category</Label>
                  <Input id="category" value={formData.category} onChange={handleChange} placeholder="Sneaker" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="brand">Brand Name</Label>
                  <Input id="brand" value={formData.brand} onChange={handleChange} placeholder="Addidas" />
                </div>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="sku">SKU</Label>
                  <Input id="sku" value={formData.sku} onChange={handleChange} placeholder="#32A53" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="stock">Stock Quantity</Label>
                  <Input id="stock" type="number" value={formData.stock} onChange={handleChange} placeholder="211" required />
                </div>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="price">Regular Price</Label>
                  <Input id="price" type="number" value={formData.price} onChange={handleChange} placeholder="₹110.40" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="salePrice">Sale Price (This will be the main price)</Label>
                  <Input id="salePrice" type="number" value={formData.salePrice} onChange={handleChange} placeholder="₹450" required />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Right Column: Product Gallery */}
          <Card>
            <CardHeader>
              <CardTitle>Product Gallery</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center justify-center w-full">
                <Label
                  htmlFor="dropzone-file"
                  className="flex flex-col items-center justify-center w-full h-64 border-2 border-dashed rounded-lg cursor-pointer bg-gray-50 hover:bg-gray-100"
                >
                  <div className="flex flex-col items-center justify-center pt-5 pb-6">
                    <Upload className="w-8 h-8 mb-4 text-gray-500" />
                    <p className="mb-2 text-sm text-gray-500">
                      <span className="font-semibold">Click to upload</span> or drag and drop
                    </p>
                    <p className="text-xs text-gray-500">PNG, JPG (MAX. 800x400px)</p>
                  </div>
                  <Input id="dropzone-file" type="file" className="hidden" />
                </Label>
              </div>
              {/* Image upload logic can be added here */}
            </CardContent>
          </Card>
        </div>
      </form>
    </RetailerLayout>
  );
}